@keyframes placeHolderShimmer {
  0%{ background-position: -468px 0; }
  100%{ background-position: 468px 0; }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(-0.5rem);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

* {
  box-sizing: border-box;
}

.error {
  width: 100%;
  height: 100vh;
  display: flex;
  padding: 15px;
  align-items: center;
  justify-content: center;
}

.error p {
  padding: 15px;
  margin: 0;
}

.row {
  width: 1140px;
  display: flex;
  margin: 0 auto;
  padding: 15px;
  line-height: 1;
}

.col {
  width: 50%;
  padding: 15px;
}

.col div:not(:last-of-type) {
  margin-bottom: 15px;
}

.loading .loaderContainer:before, .loading.loaderContainer:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background: #f5f5f5;
  border-radius: 3px;

  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  animation-name: placeHolderShimmer;
  background: linear-gradient(to right, #f5f5f5 8%, #e8e8e8 18%, #f5f5f5 33%);
  background-size: 1140px 100px;
}

.loading .loaderContainer, .loading.loaderContainer {
  background: #f5f5f5;
  border-radius: 3px;
  overflow: hidden;
  position: relative;
  transition: background 0.5s, width 0.5s, height 0.5s, border-color 0.5s;
}

.loaded .loaderContainer, .loaded.loaderContainer {
  background: initial;
}

.loaded .loaderContainer:not(.loading) > *:not(.loaderContainer), .loaded.loaderContainer > * {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 0.5s, border-color 0.5s, transform 0.5s;
}

.loading .loaderContainer:not(.loading) > *:not(.loaderContainer), .loading.loaderContainer > * {
  opacity: 0;
  transform: translateX(-0.5rem);
}

.productName {
  margin: 0 auto 1em auto;
}

.productName h1 {
  margin: 0;
}

.loading .productName {
  width: 525px;
  height: 4em;
}

.productSku {
  color: #42669e;
  min-height: 1em;
}

.loading .productSku {
  width: 50%;
}

.productSku span {
  font-weight: bold;
}

.productImage {
  border: 1px solid #e2e2e2;
  border-radius: 3px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3px;
}

.productImage.loading {
  border-color: #f5f5f5;
}

.productImage.selected {
  border-color: #42669e;
}

.productImage img {
  width: 100%;
  height: auto;
}

.productImageSelected {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 525px;
  height: 525px;
  overflow: hidden;
}

.productImageSelected .productImage {
  width: 100%;
  height: 100%;
}

.fadeIn {
  opacity: 0;
  animation: fadeIn 0.5s;
}

.productImagesThumbnails {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.loading .productImagesThumbnails {
  height: 100px;
  width: 75%;
  margin: 0 auto;
}

.productImagesThumbnails .productImage {
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.productImagesThumbnails .productImage:not(:last-of-type) {
  margin-right: 15px;
}

.loading .productPrice {
  min-height: 2em;
  width: 25%;
}

.productPrice span {
  font-size: 2em;
}

.loading .productDescription {
  height: 10em;
  width: 100%;
}

.productDescription ul:last-of-type {
  margin-bottom: 0;
}

@media all and (max-width: 767px) { 
  .col, .row, .productImageSelected, .loading .productImagesThumbnails, .loading .productName {
    width: 100%;
  }

  .col:not(:last-of-type) {
    padding-bottom: 0;
  }

  .row {
    flex-wrap: wrap;
  }

  .productImageSelected {
    height: calc(100vw - 60px);
  }

  .loading .productImagesThumbnails, .productImagesThumbnails .productImage {
    height: calc(33vw - 30px);
  }

  .productImagesThumbnails .productImage {
    width: calc(33vw - 30px);
  }

  .col div.productImage {
    margin-bottom: 0;
  }

  .loading .productSku {
    width: 75%;
}

}